import {
  IonContent, IonHeader, IonPage, IonTitle, IonToolbar, IonLabel,
  IonToast, IonSegment, IonSegmentButton, IonItem, IonInput, IonButton, IonIcon, IonCard, IonCardContent, IonImg, isPlatform
} from '@ionic/react';
import { qrCodeSharp, download, camera } from 'ionicons/icons';
import React, { useState, useEffect, useRef, useContext } from 'react';
import './Home.css';

// Libs
import QRCode from 'qrcode';

// Plugins
import { BarcodeScanner } from '@ionic-native/barcode-scanner';
import { Base64ToGallery } from '@ionic-native/base64-to-gallery';
import { AndroidPermissions } from '@ionic-native/android-permissions';
import { Camera } from '@ionic-native/camera';

// Contexts
import { WebSocketContext } from '../contexts/websocket.context';
import { ScanContext } from '../contexts/scan.context';


const Home = () => {

  const wsContext = useContext(WebSocketContext);
  const scanContext = useContext(ScanContext);
  const canvaRef = useRef();

  const [qrData, setQrData] = useState('https://club.lanacion.com.ar/');
  const [scannedCode, setscannedCode] = useState(null);
  const [scannedCodeSocket, setscannedCodeSocket] = useState([]);
  const [elementType, setElementType] = useState('canvas');
  const [urlImg, setUrlImg] = useState(null);
  const [urlPhoto, setUrlPhoto] = useState(null);
  const [showToast, setShowToast] = useState(false);

  useEffect(() => {

    if (elementType === 'canvas') {

      wsContext.init();
      const subscription = wsContext.getMessagePrivate()
        .subscribe(data => {

          scanContext.push(data);
          setscannedCodeSocket([
            ...scanContext
          ]);
        });

      return () => {

        subscription.unsubscribe();
      };
    }
  }, [elementType]);

  useEffect(() => {

    if (elementType === 'canvas' && isPlatform('mobile') && scannedCode) {

      console.log('SOCKET', scannedCode);
      wsContext.emit('sendInfo', scannedCode);
    }

  }, [scannedCode]);

  useEffect(() => {

    if (!qrData && elementType !== 'photo') { return; }

    console.log(elementType);
    switch (elementType) {

      case 'canvas':

        QRCode.toCanvas(canvaRef.current, qrData, function (error) {
          if (error) console.error(error)
          console.log('success!');
        });
        break;

      case 'img':

        QRCode.toDataURL(qrData, { type: 'image/jpeg' }, function (error, url) {

          if (error) console.error(error)
          console.log(url);
          setUrlImg(url);

        });
        break;

      case 'photo':

        setUrlPhoto(null);
        break;

      default: // url
        QRCode.toDataURL(qrData, function (error, url) {

          if (error) console.error(error)
          console.log(url);
          setUrlImg(url);

        });
    }

  }, [elementType, qrData]);

  const getPhoto = () => {

    if (isPlatform('mobile')) {

      const options = {
        quality: 100,
        destinationType: Camera.DestinationType.DATA_URL,
        encodingType: Camera.EncodingType.JPEG,
        mediaType: Camera.MediaType.PICTURE
      };

      return Camera.getPicture(options)
        .then(imageData => {

          console.log(imageData);
          setUrlPhoto('data:image/jpeg;base64,' + imageData);
        })
        .catch(console.error);
    }
    else {

      return new Promise((resolve) => {

        return setUrlPhoto('data:image/png;base64,');
      });
    }
  };

  const handleScanCode = () => {

    if (isPlatform('mobile')) {
      
      BarcodeScanner.scan().then(
        barcodeData => {
  
          setscannedCode(barcodeData.text);
        }
      );
    }
    else {

      setscannedCode('algo');
      wsContext.emit('sendInfo', 'algo');
    }
  };

  const handleDownloadQR = () => {

    const canvas = document.querySelector('canvas');
    const imageData = canvas.toDataURL('image/jpeg').toString();
    console.log('data', imageData);

    let data = imageData.split(',')[1];

    Base64ToGallery.base64ToGallery(data, {
      prefix: 'img',
      mediaScanner: true
    })
      .then(res => {

        setShowToast(true);
      })
      .catch(console.error);
  };

  const checkStoragePermission = () => {

    AndroidPermissions.checkPermission(AndroidPermissions.PERMISSION.WRITE_EXTERNAL_STORAGE).then(
      result => {
        if (result.hasPermission) {
          handleDownloadQR();
        }
        else {
          AndroidPermissions.requestPermission(AndroidPermissions.PERMISSION.WRITE_EXTERNAL_STORAGE).then(result => {
            if (result.hasPermission) {
              handleDownloadQR();
            }
          });
        }
      },
      err => AndroidPermissions.requestPermission(AndroidPermissions.PERMISSION.WRITE_EXTERNAL_STORAGE)
    );
  }

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar color="primary">
          <IonTitle>QR APP</IonTitle>
        </IonToolbar>
      </IonHeader>

      <IonContent>

        {
          elementType !== 'photo' &&
          <IonItem>
            <IonInput type="text" placeholder="Mi código QR" value={qrData} onIonChange={e => setQrData(e.detail.value)}></IonInput>
          </IonItem>
        }

        <IonSegment scrollable value={elementType} onIonChange={e => setElementType(e.detail.value)}>
          <IonSegmentButton value="canvas">
            <Ion-Label>Canvas</Ion-Label>
          </IonSegmentButton>
          <IonSegmentButton value="img">
            <Ion-Label>Image</Ion-Label>
          </IonSegmentButton>
          <IonSegmentButton value="url">
            <Ion-Label>URL</Ion-Label>
          </IonSegmentButton>
          <IonSegmentButton value="photo">
            <Ion-Label>Photo</Ion-Label>
          </IonSegmentButton>
        </IonSegment>

        {
          elementType === 'canvas' &&
          <IonButton expand="full" onClick={handleScanCode} color="secondary">
            <IonIcon icon={qrCodeSharp} slot="start"></IonIcon>
          Scan Code
        </IonButton>

        }

        {
          elementType === 'photo' &&
          <IonButton expand="full" onClick={getPhoto} color="secondary">
            <IonIcon icon={camera} slot="start"></IonIcon>
          Take Photo
        </IonButton>
        }


        {
          qrData && elementType !== 'photo' &&
          <IonCard className="ion-text-center">

            {elementType === 'canvas' && <canvas ref={canvaRef}></canvas>}
            {(elementType === 'img' || elementType === 'url') && <img src={urlImg} />}

            <IonCardContent>

              {elementType === 'canvas' &&

                <IonButton expand="full" onClick={checkStoragePermission} color="tertiary">
                  <IonIcon icon={download} slot="start"></IonIcon>
                Download QR Code
              </IonButton>
              }

            </IonCardContent>
          </IonCard>
        }

        {(elementType === 'photo' && urlPhoto) &&

          <IonCard>
            <IonCardContent>
              <IonImg src={urlPhoto} />
            </IonCardContent>
          </IonCard>

        }

        {
          scannedCode && isPlatform('mobile') &&
          <IonCard>
            <IonCardContent>
              Resultado del Scan: {scannedCode}
            </IonCardContent>
          </IonCard>
        }

        {
          scannedCodeSocket.map(s => {

            return <IonCard>
              <IonCardContent>
                Resultado del Scan Externo: {s}
              </IonCardContent>
            </IonCard>
          })
        }

        {showToast &&
          <IonToast
            isOpen={showToast}
            onDidDismiss={() => setShowToast(false)}
            message="Se guardó el QR en tu galería."
            duration={200}
          />
        }

      </IonContent>
    </IonPage>
  );
};

export default Home;
