import io from 'socket.io-client';
import { Observable, fromEvent } from 'rxjs';
import { environment } from '../environments/environment';

export class WebSocketService {

    socket = {};
    socketStatus = false;

    init() {

        this.socket = io(environment.wsUrl);
        return this.socket;
    };

    /**
     * Informa el estado del servidor
     */
    checkStatus() {

        this.socket.on('connect', () => {

            console.log('Conectado al servidor');
            this.socketStatus = true;
        });

        this.socket.on('disconnect', () => {

            console.log('Desconectado del servidor');
            this.socketStatus = false;
        });
    };

    /**
     * Envía un mensaje hacia el servidor
     * @param event Nombre del evento que deseo emitir
     * @param payload Data que quiero enviar
     * @param callback Función callback posterior a ejecutar
   */
    emit(event, payload, callback) {

        console.log('Emitiendo ', event);
        this.socket.emit(event, payload, callback);
    };

    /**
     * Escucha un evento determinado
     * @param event Nombre del evento a escuchar
    */
    listen(event) {

        return fromEvent(this.socket, event);
    };

    getMessagePrivate() {

        return this.listen('responseInfoClient');
    };

    disconnect() {

        this.socket.disconnect();
    };
}

export default WebSocketService;